import { Injectable } from '@angular/core';

@Injectable()
export class WindowRef {
  public get nativeWindow(): any {
    return this._window();
  }

  private _window(): any {
    if (window) {
      return window;
    } else {
      let winObj: any = {};
      winObj.nativeWindow = {};

      return winObj;
    }
  }
}
